'use client';

// TEAM_001: Home page showing conference list, split from prototype UI.

import { useRouter } from 'next/navigation';
import { Header, ConferenceCards } from './_components/event-ui';
import { conferences } from './_data/conferences';

export default function HomePage() {
  const router = useRouter();

  return (
    <div className="min-h-screen bg-slate-50 font-sans text-gray-900">
      <Header />
      <main>
        <ConferenceCards
          conferences={conferences}
          onRegisterClick={(conf) => router.push(`/register?confId=${conf.id}`)}
          onViewParticipants={(conf) => router.push(`/participants?confId=${conf.id}`)}
        />
      </main>
    </div>
  );
}
